@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,300italic,regular,italic,500,500italic,700,700italic);
@import url(https://fonts.googleapis.com/css?family=Roboto:100,100italic,300,300italic,regular,italic,500,500italic,700,700italic,900,900italic);
*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
  padding: 0;
  margin: 0;
}

html {
  box-sizing: border-box;
  font-family: Ubuntu, sans-serif;
  font-size: 20px;
  line-height: normal;
  font-style: normal;
  color: #333333;
  font-weight: 4000;
}

a {
  text-decoration: none;
  color: inherit;
}

.hidden {
  display: none !important;
}

.overflow {
  overflow: hidden;
}

@keyframes anim {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.burger {
  z-index: 5;
  display: none;
  position: relative;
  border: none;
  background: none;
  height: 20px;
  width: 40px;
  cursor: pointer;
}

.burger-line {
  width: 40px;
  height: 2px;
  background-color: #333;
  transition: 0.3s all ease;
}

.top-line {
  position: absolute;
  top: 0;
  right: 0;
}

.mid-line {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
}

.bottom-line {
  position: absolute;
  left: 0;
  bottom: 0;
}

.burger.active > .top-line {
  transform: translateY(8px) rotate(45deg);
  transition: 0.3s all ease;
}

.burger.active > .mid-line {
  opacity: 0;
  transition: 0.3s all ease;
}

.burger.active > .bottom-line {
  transform: translateY(-10px) rotate(-45deg);
  transition: 0.3s all ease;
}

.container {
  max-width: 1600px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

@keyframes hope {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-15px);
  }
  100% {
    transform: translateY(0px);
  }
}
