.header {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 120px;
  z-index: 11;
  background: #ffffff;
  box-shadow: 0px 3px 8px -3px rgba(0, 0, 0, 0.25);
  display: flex;
  align-items: center;
}
.header__wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  display: flex;
  align-items: center;
}
.header__logo img {
  width: 264px;
}
.header .menu {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  gap: 50px;
  list-style-type: none;
}
.header .menu__item {
  font-size: 20px;
  font-weight: 500;
  color: #333333;
  transition: color 0.3s;
  position: relative;
}
.header .menu__item::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background: #111;
  left: 0;
  bottom: -5px;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.5s;
}
.header .menu__item:hover {
  color: #111;
}
.header .menu__item:hover::before {
  transform: scaleX(1);
  transform-origin: left;
}
.header .menu__item.none::before {
  content: "";
  position: static;
  width: 0%;
  height: 0px;
  background: #ffffff;
  left: 0;
  bottom: 0px;
  transform: scaleX(0);
  transform-origin: none;
  transition: none;
}
.header .menu__item.none:hover::before {
  transform: scaleX(0);
  transform-origin: left;
}
.header .menu__github {
  display: flex;
  align-items: center;
}
.header .menu__github img {
  opacity: 1;
  width: 72px;
  transition: opacity 0.3s;
}
.header .menu__github img:hover {
  opacity: 0.85;
}
.header .hidden {
  display: none;
}

.mobile {
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #DA142E;
  display: flex;
  height: 100vh;
  flex-direction: column;
  align-items: center;
  gap: 120px;
  animation: anim 1s ease;
}
.mobile__items {
  margin: 0;
  padding: 0;
  margin-top: 160px;
  list-style-type: none;
  display: flex;
  gap: 45px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.mobile__item {
  font-size: 20px;
  font-weight: 500;
  color: #ffffff;
  transition: color 0.3s;
  position: relative;
}
.mobile__item::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background: #ffffff;
  left: 0;
  bottom: -5px;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.5s;
}
.mobile__item:hover {
  color: #e7e7e7;
}
.mobile__item:hover::before {
  transform: scaleX(1);
  transform-origin: left;
}
.mobile__contact {
  display: flex;
  align-items: center;
  gap: 30px;
}
.mobile__icon {
  object-fit: cover;
  height: 32px;
  opacity: 1;
  transform: translateY(0px);
  transition: all 0.3s;
}
.mobile__icon:hover {
  opacity: 0.9;
  transform: translateY(-5px);
}

