.hero {
  position: relative;
  height: auto;
  background-image: url("../img/hero-bg.webp");
  background-repeat: no-repeat;
  background-position: right top -20px;
  background-size: 1050px;
  padding-bottom: 100px;
}
.hero .container {
  padding-top: 220px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.hero__img {
  pointer-events: none;
  width: 750px;
}
.hero__title {
  font-weight: 700;
  font-size: 56px;
  color: #000000;
}
.hero__descr {
  padding-top: 50px;
  max-width: 600px;
  font-weight: 400;
  font-size: 28px;
  color: #000000;
}
.hero__btn {
  margin-top: 200px;
  font-weight: 500;
  font-size: 28px;
  color: #000000;
  background: linear-gradient(90deg, #F5F7FA 0%, #C3CFE2 100%);
  border-radius: 5px;
  cursor: pointer;
  transform: translateY(0px);
  transition: transform 0.3s;
  padding: 28px 68px;
  border: none;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.hero__btn:hover {
  transform: translateY(-10px);
}
.hero__arrow {
  position: absolute;
  bottom: 0%;
  right: 8%;
  cursor: pointer;
  opacity: 1;
  animation: hope 4s;
  transform: translateY(0px);
  animation-iteration-count: infinite;
  transition: all 0.3s;
}
.hero__arrow img {
  width: 64px;
}
.hero__arrow:hover {
  opacity: 0.8;
}

